@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cookie&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  background-image: url(/static/media/background.72388d5b.webp);
  background-position: center;
  background-size: cover;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.login__button{
  background-color: #f95d67  !important ;
  color:#fff
}
.login__button:hover{
  background-color: #ec4c57 !important ;
}
.heading__box{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #fff;
  width: 100%;
  margin: 40px 0 15px 0;
  border-radius: 5px;
  border: 1px solid #ffffff ;
}

.app__heading{
  font-size: 3rem;
  font-family: "Cookie";
}



.css-k42uhu[aria-selected=true], .css-k42uhu[data-selected]{
  background: #f57079  !important;
  color: #fff !important;
  box-shadow: none !important;
}
.messages {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: none;
}

